.sec1__container,.sec2__container{margin-top:70px}@media (max-width: 425px){.sec1__container,.sec2__container{margin-top:0;display:flex;flex-direction:column;justify-content:center}}@media (max-width: 991px){.sec1__container .sec1__content p,.sec2__container .sec1__content p{position:relative;top:50%;left:50%;transform:translate(-50%, -50%)}}@media (max-width: 425px){.sec1__container .sec1__content p,.sec2__container .sec1__content p{display:none}}.sec1__container .sec1__content .sec1__content__mb,.sec2__container .sec1__content .sec1__content__mb{display:none}@media (max-width: 425px){.sec1__container .sec1__content .sec1__content__mb,.sec2__container .sec1__content .sec1__content__mb{display:block}}.sec1__container .sec1__content img,.sec2__container .sec1__content img{width:600px;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6))}@media (max-width: 425px){.sec1__container .sec1__content img,.sec2__container .sec1__content img{display:none}}@media (max-width: 991px){.sec2__content #sec2__content__adjust{padding-top:0 !important}}.sec2__content img{width:500px;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6))}@media (max-width: 425px){.sec2__content img{display:none}}@media (max-width: 991px){.sec2__content h2,.sec2__content p{position:relative;top:50%;left:50%;transform:translate(-50%, -50%)}}@media (max-width: 425px){.sec2__content h2,.sec2__content p{display:none}}.sec2__content .sec2__content__mb{display:none}@media (max-width: 425px){.sec2__content .sec2__content__mb{display:flex;flex-direction:column;justify-content:space-between}}@media (max-width: 425px){.sec2__content .sec2__content__mb h2{display:block;font-size:20px;margin-bottom:30px}}@media (max-width: 425px){.sec2__content .sec2__content__mb p{display:block;margin-top:20px;margin-bottom:0}}@media (max-width: 425px){.sec2__content .sec2__content__mb img{display:block;margin-top:0;width:300px;align-self:center}}.sec3__content{display:flex;flex-direction:column;align-items:center}.sec3__content img{width:500px}@media (max-width: 425px){.sec3__content img{width:300px;align-self:center}}.sec4__btns{display:flex;justify-content:space-around;align-items:center !important;width:300px}.fp-controlArrow.fp-next{right:15px;border-width:38.5px 0 38.5px 34px;border-color:transparent transparent transparent rgba(0,0,0,0.6)}.fp-controlArrow.fp-prev{left:15px;width:0;border-width:38.5px 34px 38.5px 0;border-color:transparent rgba(0,0,0,0.6) transparent transparent}

.profile__bg{background-repeat:no-repeat;background-size:100% 100%;height:500px;width:100%;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));margin-bottom:50px}@media (max-width: 425px){.profile__bg{background-repeat:no-repeat;background-size:cover;background-position:center;height:500px;width:100%;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));margin-bottom:50px}}.profile__top{display:flex;flex-direction:column;justify-content:center}.profile__top .profile__photo{margin-top:150px !important;width:200px;height:200px;border-radius:50%}.profile__top .profile__top__header{color:white !important;font-weight:800 !important;text-shadow:1px 0 0 black, 0 1px 0 black, -1px 0 0 black, 0 -1px 0 black}.profile__item{padding:10px 0 15px 0}.profile__btn__container{flex-direction:column}.profile__btn__container a{text-decoration:none;color:white !important;margin-bottom:15px}.userPublic__btn__container{margin-bottom:5px}.profile__itemStatus{display:flex;flex-direction:column;justify-content:center;align-items:center}

.random__image{object-fit:contain;height:400px}@media (max-width: 425px){.random__image{height:300px}}#random__text{font-size:24px}@media (max-width: 425px){#random__text{font-size:14px;width:30%}}.items__container{margin-top:50px !important}@media (max-width: 425px){.latest__grid{left:10%}}.latest__image{max-height:300px}.location__header{margin-left:20px;padding-bottom:30px}@media (max-width: 425px){.location__header{text-align:center;margin-bottom:50px}}.itemDetail__img__container{display:flex;justify-content:center;align-items:center}.itemDetail__img{-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));object-fit:contain;max-height:500px}@media (max-width: 425px){.itemDetail__img{width:100%;-webkit-filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6));filter:drop-shadow(5px 5px 5px rgba(0,0,0,0.6))}}.itemDetail__location{font-size:20px}@media (max-width: 425px){.itemDetail__location{font-size:15px}}.itemDetail__description{margin-top:10px}.itemDetail__btn{margin-top:15px}.goback{background-color:Transparent;background-repeat:no-repeat;border:none;cursor:pointer;overflow:hidden;outline:none;animation:slide 2s ease-in-out .5s infinite alternate;width:100%}.goback:hover{transform:translateY(3px)}.goback:active{transform:translateY(6px);transition-duration:.1s}.itemList__container{margin-top:50px}

.inbox__container{width:750px;height:800px;background:#eff3f7;margin:90px auto 0 auto;font-size:0;border-radius:5px;overflow:hidden}@media (max-width: 425px){.inbox__container{display:none}}.inbox__header{padding:20px 0;margin-bottom:10px;border-bottom:1px solid white;text-align:center}.inbox__header button h2{color:white}.inbox__fromList__user,.inbox__mobile__fromList{padding-left:10px !important;margin-left:10px !important}.inbox__chatBox__header{display:flex;justify-content:space-around;align-items:center}.inbox__chatBox__alert{display:flex;flex-direction:column}.inbox__chatBox__alert .inbox__chatBox__text__alert{color:#fa5246;padding-left:20px}.inbox__chatBox__alert .inbox__chatBox__text__success{color:#2cb67d;padding-left:20px}.inbox__mobile__container{display:none}@media (max-width: 425px){.inbox__mobile__container{display:block;margin-top:10px !important}}@media (max-width: 425px){.inbox__mobile__header{background-color:#3b3e49;width:100%;height:100px;display:flex;flex-direction:column;justify-content:center}}@media (max-width: 425px){.inbox__mobile__header h1{color:#ffff}}.inbox__mobile__fromList{display:flex !important;justify-content:flex-start !important}.inbox__mobile__fromList__content{display:flex;flex-direction:column}.chat__mobile__header{display:flex;align-items:center;border-bottom:1px solid grey}.inbox__chatBox__header__btn{padding-left:10px}.inbox__chatBox__header__btn button{padding-left:10px}.chat__mobile__container{display:none}@media (max-width: 425px){.chat__mobile__container{display:block}}.inbox__btn p{font-size:16px;color:white}.inbox__link{background-color:Transparent;background-repeat:no-repeat;border:none;cursor:pointer;overflow:hidden;outline:none}.inbox__userList__text{color:white;font-size:11px}.inbox__mobile__btn,.inbox__btn{background-color:Transparent;background-repeat:no-repeat;border:none;cursor:pointer;overflow:hidden;outline:none}.inbox__userList{width:260px;height:800px;background-color:#3b3e49;display:inline-block;font-size:15px;vertical-align:top}.inbox__chatBox{width:490px;height:800px;display:inline-block;font-size:15px;vertical-align:top}.inbox__chatBox__container{padding-left:0;margin:0;list-style-type:none;overflow-y:scroll;height:535px;border-top:2px solid #fff;border-bottom:2px solid #fff}.inbox__chatBox__container .inbox__me,.inbox__chatBox__container .inbox__you{padding:10px 30px}.inbox__chatBox__container .inbox__me{text-align:right}.inbox__chatBox__container .inbox__me .inbox__text{background-color:#6fbced}.inbox__chatBox__container .inbox__me .inbox__triangle{border-color:transparent transparent #6fbced transparent;margin-left:375px;margin-right:10px}.inbox__chatBox__container .inbox__you .inbox__text{background-color:#58b666}.inbox__chatBox__container .inbox__you .inbox__triangle{border-color:transparent transparent #58b666 transparent;margin-left:15px}.inbox__chatBox__container h2{display:inline-block;font-size:13px;font-weight:normal}.inbox__chatBox__container .inbox__entete{margin-bottom:5px}.inbox__chatBox__container .inbox__triangle{width:0;height:0;border-style:solid;border-width:0 10px 10px 10px}.inbox__chatBox__container .inbox__text{padding:20px;color:#fff;line-height:25px;max-width:90%;display:inline-block;text-align:left;border-radius:5px}.inbox__messageForm{height:155px;padding:20px 30px 10px 20px;border-top:1px solid grey}.message__form{display:flex;justify-content:center;align-items:center}.inbox__messageForm__input{resize:none;border:none;width:100%;height:80px;border-radius:3px;padding:20px 10px 20px 20px;font-size:13px}.inbox__messageForm__btn{text-decoration:none;vertical-align:top;display:inline-block}.chat__noChat__text{text-align:center}

.NotFound__body,.loading__body{display:flex;width:100vw;height:100vh;align-items:center;justify-content:center;margin:0}.NotFound__body{background:#373A47;color:#fff;font-size:96px;font-family:'Fira Mono', monospace;letter-spacing:-7px}.NotFound__body .NotFound__main{animation:glitch 1s linear infinite}@keyframes glitch{2%,64%{transform:translate(2px, 0) skew(0deg)}4%,60%{transform:translate(-2px, 0) skew(0deg)}62%{transform:translate(0, 0) skew(5deg)}}.NotFound__main:before,.NotFound__main:after{content:attr(title);position:absolute;left:0}.NotFound__main:before{animation:glitchTop 1s linear infinite;clip-path:polygon(0 0, 100% 0, 100% 33%, 0 33%);-webkit-clip-path:polygon(0 0, 100% 0, 100% 33%, 0 33%)}@keyframes glitchTop{2%,64%{transform:translate(2px, -2px)}4%,60%{transform:translate(-2px, 2px)}62%{transform:translate(13px, -1px) skew(-13deg)}}.NotFound__main:after{animation:glitchBotom 1.5s linear infinite;clip-path:polygon(0 67%, 100% 67%, 100% 100%, 0 100%);-webkit-clip-path:polygon(0 67%, 100% 67%, 100% 100%, 0 100%)}@keyframes glitchBotom{2%,64%{transform:translate(-2px, 0)}4%,60%{transform:translate(-2px, 0)}62%{transform:translate(-22px, 5px) skew(21deg)}}

