@import "./breakpoints.scss";


.form__container,
.chat__noChat {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    min-height: 100vh;
}


.form__form {
    border-radius: 10%;
    padding: 40px 40px 0 40px;

    @include media("<=phone") {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        margin-top: 60%
    }
}

.form__upload {
    border-radius: 10%;
    padding: 40px 40px 0 40px;

    @include media("<=phone") {
        padding: 0 40px 0 40px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        margin-top: 60%
    }
}


.form__editUser {
    border-radius: 10%;
    padding: 40px 40px 0 40px;

    @include media("<=phone") {
        padding: 0 40px 0 40px;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        margin-top: 30%
    }
}

.form__button {
    width: 100%;
}

.form__right {
    text-align: right;
    float: right;
    padding: 20px 50px 0 0;
}



.form__alert {
    color: #fa5246;
    font-weight: 800;
    padding-left: 5px;
}

.form__available {
    color: #2cb67d;
    font-weight: 800;
    padding-left: 5px;
}

.form__testMode__container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
    padding: 0 40px 0 40px;

}


.form__edit__container {
    margin: 10px 0 10px 0 !important;
}