@import "./breakpoints.scss";


.sec1__container,
.sec2__container {
    margin-top: 70px;

    @include media("<=phone") {
        margin-top: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;

    }

    .sec1__content {

        p {

            @media(max-width: 991px) {
                position: relative;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }


            @include media("<=phone") {
                display: none;
            }
        }

        .sec1__content__mb {
            display: none;

            @include media("<=phone") {
                display: block;
            }
        }

        img {
            width: 600px;
            filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.6));

            @include media("<=phone") {
                display: none;
            }
        }


    }
}

.sec2__content {

    #sec2__content__adjust {
        @media(max-width: 991px) {
            padding-top: 0 !important;
        }
    }


    img {
        width: 500px;
        filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.6));

        @include media("<=phone") {
            display: none;
        }
    }

    h2,
    p {
        @media(max-width: 991px) {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        @include media("<=phone") {
            display: none;
        }
    }


    .sec2__content__mb {
        display: none;

        @include media("<=phone") {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }


        h2 {
            @include media("<=phone") {
                display: block;
                font-size: 20px;
                margin-bottom: 30px;
            }
        }

        p {
            @include media("<=phone") {
                display: block;
                margin-top: 20px;
                margin-bottom: 0;
            }
        }

        img {

            @include media("<=phone") {
                display: block;
                margin-top: 0;
                width: 300px;
                align-self: center;
            }
        }
    }
}

.sec3__content {
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
        width: 500px;

        @include media("<=phone") {

            width: 300px;
            align-self: center;
        }
    }
}

.sec4__btns {
    display: flex;
    justify-content: space-around;
    align-items: center !important;
    width: 300px;
}

.fp-controlArrow.fp-next {
    right: 15px;
    border-width: 38.5px 0 38.5px 34px;
    border-color: transparent transparent transparent rgba(0, 0, 0, 0.6);
}

.fp-controlArrow.fp-prev {
    left: 15px;
    width: 0;
    border-width: 38.5px 34px 38.5px 0;
    border-color: transparent rgba(0, 0, 0, 0.6) transparent transparent;
}